import EditDisabledIcon from "assets/img/buttonIcons/Disabled_Edit.svg";
import EditIcon from "assets/img/buttonIcons/Edit.svg";
import LoadingIcon from "assets/img/common/Loading.svg";
import { AxiosResponse } from "axios";
import AutoComplete from "components/AutoComplete/AutoComplete";
import ButtonBox from "components/ButtonBox/ButtonBox";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import IconButton from "components/IconButton/IconButton";
import InputBox from "components/InputBox/InputBox";
import MenuBox from "components/MenuBox/MenuBox";
import ReadOnlyTable from "components/ReadOnlyTable/ReadOnlyTable";
import SectionTitle from "components/SectionTitle/SectionTitle";
import SimpleDropDown from "components/SimpleDropDown/SimpleDropDown";
import Text from "components/Text/Text";
import { RouteProps, withRouter } from "hoc/withRouter";
import { useTranslateText } from "hook/CustomHooks";
import { numberFormatter } from "locale";
import { CalendarChangeParams } from "primereact/calendar";
import { Column } from "primereact/column";
import { ChangeEvent, useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { linkAuthRoute, linkBasePath } from "routes";
import LoanService from "services/transaction/LoanService";
import {
  LINK_DDE_STAGE_AND_STATUS,
  LINK_DDE_STAGE_AND_STATUS_STAGES,
  LINK_LOAN_DETAIL,
  LINK_QDE,
} from "transactionRoutes";
import {
  currencyFormatToNumberForFilter,
  formatDate,
  parseStandardAPIErrorMessage,
} from "utils/CommonUtil";
import {
  BUTTON_TYPE,
  DROPDOWN_STYLE_TYPE,
  FEE_MODE,
  FEE_MODE_LABEL,
  FEE_RECEIPT_STATUS,
  FEE_RECEIPT_STATUS_LABEL,
  FEE_RECEIPT_STATUS_OPTIONS,
  INPUT_STYLE_TYPE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
import OrgStructureBranchService from "services/masterdata/OrgStruct/Branch/Branch";
import {
  IDropDownOptions,
  enrichForNewReceipt as enrichForSave,
} from "views/transaction/PageDDE/Login/Helper";
import DatePicker from "components/DatePicker/DatePicker";
import FeeService from "services/transaction/FeeService";
import WarningDialog from "components/WarningDialog/WarningDialog";
import {
  ILoginFeeReceiptRowData,
  TLoginReceiptStatusType,
} from "views/transaction/PageDDE/Login/PageFees/PageFees";
import { updateReceiptDetailsPageObj } from "../../PageDDE/Login/Helper";
import AddNewReceiptDialog, {
  IAddNewReceiptDialogRef,
  ILoginFeeReceiptData,
  MODULE_FOR_ADD_NEW_RECEIPT,
  initialNewReceiptData,
} from "components/AddNewReceiptDialog/AddNewReceiptDialog";
import SharePaymentDetailsDialog from "components/SharePaymentDetailsDialog/SharePaymentDetails";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import ToastCustom from "components/Toast";
import { getReceiptStatusLabel } from "../Helper";
import SystemDatePicker from "components/SystemDatePicker/SystemDatePicker";
interface IAutocomplete {
  name?: string;
  uid?: string;
  receiptRefNo?: string;
}
interface IMenuModal {
  className: string;
  label: string;
  receiptUid?: string;
  command?: () => void;
  disabled?: boolean;
}

interface IFilter {
  loan: IAutocomplete;
  receipt: IAutocomplete;
  branch: IAutocomplete;
  applicant: IAutocomplete;
  instrumentType: string;
  instrumentDateGreaterThenEqualTo: string;
  instrumentDateLessThenEqualTo: string;
  amountCollectedGreaterThenEqualTo: string;
  amountCollectedLessThenEqualTo: string;
  status: string;
}

export interface IFeeCollectionReceiptRowData {
  uid: string;
  loan: {
    uid: string;
    lanId: string;
    applicantName: string;
    branch: {
      uid: string;
      name: string;
    };
  };
  dateOfStatus: string;
  receiptRefNo: string;
  smInstrumentType: string;
  instrumentDate: string;
  txnRefNo: string;
  amount: number;
  status: TLoginReceiptStatusType | "";
  remarks: string;
  createdByUserName?: string; // not sure about key
  updatedAtSystemDate?: string;
  updatedAt?: string;
}
interface IPageObj {
  showFilter: boolean;
  filter: IFilter;
  tableSearch: string;
  sharePaymentDialog: boolean;
  deleteReceipt: {
    showPopUp: boolean;
    uid: string;
  };
  dropdownOptions: {
    instrumentType: IDropDownOptions[];
    // instrumentType:IDropDownOptions[];
  };
  editReceipt: ILoginFeeReceiptData;
  editReceiptDialog: boolean;
  isReceiptDataLoading: boolean;
}
const initialFilter: IFilter = {
  loan: {
    name: "",
    uid: "",
  },
  receipt: {
    receiptRefNo: "",
  },
  branch: {
    name: "",
    uid: "",
  },
  applicant: {
    name: "",
    uid: "",
  },
  instrumentType: "",
  instrumentDateGreaterThenEqualTo: "",
  instrumentDateLessThenEqualTo: "",
  amountCollectedGreaterThenEqualTo: "",
  amountCollectedLessThenEqualTo: "",
  status: "",
};
const initialPageObj: IPageObj = {
  showFilter: true,
  filter: {
    ...initialFilter,
  },
  tableSearch: ``,
  dropdownOptions: {
    instrumentType: [],
  },
  deleteReceipt: {
    showPopUp: false,
    uid: "",
  },
  sharePaymentDialog: false,
  editReceiptDialog: false,
  editReceipt: {
    ...initialNewReceiptData,
  },
  isReceiptDataLoading: false,
};

const PageReceipts = (props: RouteProps) => {
  /*
    -------------------------------------------------------
      State
    -------------------------------------------------------
    */

  const [pageObj, setPageObj] = useState<IPageObj>({ ...initialPageObj });
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessage, setToastMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [suggestionsTemplate, setSuggestionsTemplate] = useState<any>([{ name: "" }]);
  const [receiptMenuModel, setReceiptMenuModel] = useState<IMenuModal[]>([
    {
      className: "px-1.5 text-xs text-primary-labelColor h-6.5 hover:bg-primary-sapphireBlue-100",
      label: "Delete",
      receiptUid: "",
      command: function () {
        onDeleteReceipt("");
        console.log("delete");
      },
    },
  ]);
  const dataTableRef = useRef<any>(null);
  const receiptDeleteMenuRef = useRef<any>(null);
  const toastMessageRef = useRef(null);
  const addReceiptRef = useRef<IAddNewReceiptDialogRef>(null);
  const dispatch = useDispatch();

  // --------------------------- global variables ---------------------- //

  /*
     -------------------------------------------------------
       useEffect
     -------------------------------------------------------
   */
  useEffect(() => {
    dataTableRef?.current?.search();
    getInstrumentType();
  }, []);

  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);

  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
      setToastMessage("");
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  useEffect(() => {
    dataTableRef?.current?.search();
  }, [pageObj?.tableSearch]);

  useEffect(() => {
    console.log("pageObj?.editReceipt ", pageObj?.editReceipt);
  }, [pageObj?.editReceipt]);

  /*
    -------------------------------------------------------
      Service Calls
    -------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Handlers
    -------------------------------------------------------
  */
  const handleFilterChange = (event: ChangeEvent<HTMLInputElement> | CalendarChangeParams) => {
    const { name, value } = event.target;
    setPageObj((prevObj) => ({
      ...prevObj,
      filter: {
        ...prevObj.filter,
        [name]: value,
      },
    }));
  };

  const handleAmountFilterChange = (event: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = event.target;
    const unformattedValue: number | string = currencyFormatToNumberForFilter(value ?? "");
    setPageObj((prevObj) => ({
      ...prevObj,
      filter: {
        ...prevObj.filter,
        [name]:
          typeof unformattedValue === "number" ? numberFormatter(unformattedValue, "en-IN", 0) : "",
      },
    }));
  };

  const handleAutoCompleteLanNoChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value.lanId, uid: value.uid },
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value, uid: "" },
        },
      }));
    }
  };

  const handleAutoCompleteBranchChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value?.name },
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value },
        },
      }));
    }
  };

  const handleAutoCompleteApplicantChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value?.applicantName },
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { name: value },
        },
      }));
    }
  };

  const handleAutoCompleteReceiptChange = (event: any) => {
    const { value } = event.target;
    if (typeof value === "object") {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { receiptRefNo: value.receiptRefNo },
        },
      }));
    } else {
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        filter: {
          ...prevPageObj.filter,
          // amountCollectedGreaterThenEqualTo:""
          [event.target.name]: { receiptRefNo: value },
        },
      }));
    }
  };

  /* 
    --------------------------------------------------------
    Validators:
    --------------------------------------------------------
  */

  /*
    -------------------------------------------------------
      Button Click Handlers
    -------------------------------------------------------
  */
  const onReset = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      filter: { ...prevPageObj.filter, ...initialFilter },
    }));
  };

  const onSearch = () => {
    let search = ``;
    if (pageObj?.filter?.loan?.name) {
      search = `${search}&loan.lanId=${pageObj?.filter?.loan?.name}`;
    }
    if (pageObj?.filter?.receipt?.receiptRefNo) {
      search = `${search}&receiptRefNo=${pageObj?.filter?.receipt?.receiptRefNo}`;
    }
    if (pageObj?.filter?.branch?.name) {
      search = `${search}&loan.branch.name=${pageObj?.filter?.branch?.name}`;
    }
    if (pageObj?.filter?.applicant?.name) {
      search = `${search}&loan.applicantName=${pageObj?.filter?.applicant?.name}`;
    }
    if (pageObj?.filter?.instrumentType) {
      search = `${search}&smInstrumentType=${pageObj?.filter?.instrumentType}`;
    }
    if (pageObj?.filter?.instrumentDateLessThenEqualTo) {
      search = `${search}&instrumentDate:le=${new Date(
        pageObj?.filter?.instrumentDateLessThenEqualTo
      )?.toISOString()}`;
    }
    if (pageObj?.filter?.instrumentDateGreaterThenEqualTo) {
      search = `${search}&instrumentDate:ge=${new Date(
        pageObj?.filter?.instrumentDateGreaterThenEqualTo
      )?.toISOString()}`;
    }
    if (currencyFormatToNumberForFilter(pageObj?.filter?.amountCollectedGreaterThenEqualTo)) {
      search = `${search}&amount:le=${currencyFormatToNumberForFilter(
        pageObj?.filter?.amountCollectedGreaterThenEqualTo
      )}`;
    }
    if (currencyFormatToNumberForFilter(pageObj?.filter?.amountCollectedLessThenEqualTo)) {
      search = `${search}&amount:ge=${currencyFormatToNumberForFilter(
        pageObj?.filter?.amountCollectedGreaterThenEqualTo
      )}`;
    }
    if (pageObj?.filter?.status) {
      search = `${search}&status=${pageObj?.filter?.status}`;
    }
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      tableSearch: search,
    }));
  };

  const onToggleFilter = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      showFilter: !prevPageObj.showFilter,
    }));
  };

  const onToggleSharePymtDialog = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      sharePaymentDialog: !prevPageObj.sharePaymentDialog,
    }));
  };

  const onCreate = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      editReceiptDialog: true,
    }));
  };

  const onToggleReceiptDeleteMenu = (e: any, rowData: IFeeCollectionReceiptRowData) => {
    receiptDeleteMenuRef?.current?.toggle?.(e);

    setReceiptMenuModel((prevObj) => {
      const updatedMenuModel = [...prevObj];
      updatedMenuModel[0] = {
        ...updatedMenuModel[0],
        command: () => {
          // write a function for delete
          onDeleteReceipt(rowData?.uid);
          // console.log("delete");
        },
      };
      return updatedMenuModel;
    });
  };

  const onResetWarningPop = () => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      deleteReceipt: {
        ...prevPageObj.deleteReceipt,
        showPopUp: false,
        uid: "",
      },
    }));
  };

  const onDeleteReceiptConfirm = async () => {
    const enrichObj = enrichForDelete();
    // call delete api
    try {
      await FeeService.updateFeeCollectionReceipt(enrichObj);
      setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
      setTriggerToast(!triggerToast);
      dataTableRef?.current?.search();
      onResetWarningPop();
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
    }
  };

  const onEditReceipt = async (uid: string) => {
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      isReceiptDataLoading: true,
    }));
    try {
      const res = await FeeService.feeCollectionReceiptDetails(uid);
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        editReceipt: updateReceiptDetailsPageObj(res?.data),
        isReceiptDataLoading: false,
        editReceiptDialog: true,
      }));
    } catch (err) {
      console.log(err);
      setErrorMessage(parseStandardAPIErrorMessage(err));
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        isReceiptDataLoading: false,
        editReceiptDialog: false,
      }));
    }
    // setPageObj((prevData) => ({
    //   ...prevData,
    //   editReceipt: {
    //     ...prevData.editReceipt,
    //     ...updateReceiptDetailsPageObj(rawData),
    //   },
    // }));
  };

  const onResetEditReceipt = () => {
    setPageObj((pageObj) => ({
      ...pageObj,
      editReceipt: {
        ...pageObj?.editReceipt,
        ...initialNewReceiptData,
      },
      editReceiptDialog: false,
    }));
  };

  const onSaveReceipt = async (data: ILoginFeeReceiptData, deleteReceipt?: boolean) => {
    const payload = enrichForSave(data, deleteReceipt);
    if (payload) {
      try {
        let service;
        if (payload?.uid) {
          service = FeeService.updateFeeCollectionReceipt;
        } else {
          service = FeeService.createFeeCollectionReceipt;
        }
        await service?.(payload);
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
        setTriggerToast(!triggerToast);
        dataTableRef?.current?.search();
        addReceiptRef.current?.onCloseAndClearData();
        onResetEditReceipt();
      } catch (err) {
        console.log(err);
        setErrorMessage(parseStandardAPIErrorMessage(err));
      }
    }
  };

  /*
    -------------------------------------------------------
      Helper Functions
    -------------------------------------------------------
  */
  const enrichForDelete = () => {
    return {
      uid: pageObj.deleteReceipt?.uid,
      isActive: false,
    };
  };

  const onDeleteReceipt = (uid: string) => {
    setPageObj((prevObj) => ({
      ...prevObj,
      deleteReceipt: {
        ...prevObj.deleteReceipt,
        showPopUp: true,
        uid: uid,
      },
    }));
  };

  const searchAutoComplete = async (
    event: any,
    options: {
      service: any;
      field: string;
      search: string;
    }
  ) => {
    let suggestionsTemplate = [];
    await options
      ?.service?.(0, 10, `${options.field}:lk=${event.query}${options?.search}`)
      .then((res: any) => {
        if (res.status === 200 && Array.isArray(res.data.results)) {
          suggestionsTemplate = [...res.data.results];
          // setIsLoading(false);
        }
      });
    if (suggestionsTemplate.length === 0) {
      suggestionsTemplate.push({ name: "" });
    }
    setSuggestionsTemplate(suggestionsTemplate);
  };

  const getInstrumentType = async () => {
    try {
      const res = await StaticMasterDataService.getDropdownList("FEE_MODE");
      const options: any = [];
      res?.data?.results.forEach((instrumentType: any) => {
        options.push({ name: instrumentType?.name ?? "", value: instrumentType?.code });
      });
      setPageObj((prevPageObj) => ({
        ...prevPageObj,
        dropdownOptions: {
          ...prevPageObj.dropdownOptions,
          instrumentType: options,
        },
      }));
    } catch (err) {
      console.error(err);
    }
  };

  /*
    -------------------------------------------------------
      Sectional Rendering
    -------------------------------------------------------
  */

  const itemTemplateApplicationNo = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.lanId}`} />
      </div>
    );
  };

  const itemTemplateBranch = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item.name}`} />
        {/* <Text className="text-secondary-gray-500 text-xxs" label={item.email} /> */}
      </div>
    );
  };

  const itemTemplateReceipt = (item: any) => {
    if (item?.receiptRefNo === undefined || item?.receiptRefNo === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item?.receiptRefNo ?? ""}`} />
      </div>
    );
  };

  const itemTemplateApplicantName = (item: any) => {
    if (item?.uid === undefined || item?.uid === "") {
      return (
        <div className="flex flex-col items-center justify-center h-39 pointer-events-none cursor-default">
          <Text className="text-primary-labelColor mt-2" label="label.noDataFound" />
        </div>
      );
    }
    return (
      <div className="flex flex-col">
        <Text className="text-primary-labelColor" label={`${item?.applicantName ?? ""}`} />
      </div>
    );
  };

  const renderFilter = () => {
    return (
      <div className="p-3 flex flex-col gap-6 bg-primary-sapphireBlue-100 rounded-lg">
        <div className="flex ">
          <SectionTitle title="text.masterData.types.search" />
        </div>
        <div className=" grid grid-cols-1 md:grid-cols-4 gap-3">
          <div>
            <AutoComplete
              label="label.fees.applicationNo"
              name="loan"
              field="name"
              value={pageObj?.filter?.loan?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "&lanId",
                  search: "&loanStatus:nq=DRAFT",
                  service: LoanService.getAll,
                })
              }
              onChange={handleAutoCompleteLanNoChange}
              itemTemplate={itemTemplateApplicationNo}
            />
          </div>
          <div>
            <AutoComplete
              label="label.fees.receiptID"
              name="receipt"
              field="name"
              value={pageObj?.filter?.receipt?.receiptRefNo}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "receiptRefNo",
                  search: "",
                  service: FeeService.feeCollectionSearchReceipt,
                })
              }
              onChange={handleAutoCompleteReceiptChange}
              itemTemplate={itemTemplateReceipt}
            />
          </div>
          <div>
            <AutoComplete
              label="label.fees.branch"
              name="branch"
              field="name"
              value={pageObj?.filter?.branch?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "name",
                  search: "",
                  service: OrgStructureBranchService.getAll,
                })
              }
              onChange={handleAutoCompleteBranchChange}
              itemTemplate={itemTemplateBranch}
            />
          </div>

          <div>
            <AutoComplete
              label="label.fees.applicantName"
              name="applicant"
              field="name"
              value={pageObj?.filter?.applicant?.name}
              variant={INPUT_STYLE_TYPE.PRIMARY}
              suggestions={suggestionsTemplate}
              completeMethod={(e: any) =>
                searchAutoComplete(e, {
                  field: "&applicantName",
                  search: "&loanStatus:nq=DRAFT",
                  service: LoanService.getAll,
                })
              }
              onChange={handleAutoCompleteApplicantChange}
              itemTemplate={itemTemplateApplicantName}
            />
          </div>
          <div>
            <SimpleDropDown
              label="label.fees.instrumentType"
              placeholder="placeholder.fees.instrumentType"
              name="instrumentType"
              value={pageObj?.filter?.instrumentType}
              onChange={handleFilterChange}
              options={pageObj?.dropdownOptions?.instrumentType}
              optionLabel="name"
              optionValue="value"
            />
          </div>
          <div>
            <SystemDatePicker
              label={"label.fees.instrumentDate>="}
              name="instrumentDateGreaterThenEqualTo"
              onChange={handleFilterChange}
              value={
                pageObj?.filter?.instrumentDateGreaterThenEqualTo
                  ? new Date(pageObj?.filter?.instrumentDateGreaterThenEqualTo)
                  : null
              }
              variant={INPUT_STYLE_TYPE.SECONDARY}
            />
          </div>
          <div className="grid-group">
            <div className="mb-1 h-4.75 flex">
              <span className="font-sans text-primary-labelColor text-xs">
                Instrument Date&lt;=
              </span>
            </div>
            <SystemDatePicker
              hideLabel
              name="instrumentDateLessThenEqualTo"
              onChange={handleFilterChange}
              value={
                pageObj?.filter?.instrumentDateLessThenEqualTo
                  ? new Date(pageObj?.filter?.instrumentDateLessThenEqualTo)
                  : null
              }
              variant={INPUT_STYLE_TYPE.SECONDARY}
            />
          </div>
          <div>
            <InputBox
              label="label.fees.amountCollected>="
              name="amountCollectedGreaterThenEqualTo"
              value={pageObj?.filter?.amountCollectedGreaterThenEqualTo}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAmountFilterChange}
            />
          </div>
          <div>
            <div className="mb-1 h-4.75 flex">
              <span className="font-sans text-primary-labelColor text-xs">
                Amount Collected&lt;=
              </span>
            </div>
            <InputBox
              // label="label.fees.amountCollected<="
              hideLabel
              name="amountCollectedLessThenEqualTo"
              value={pageObj?.filter?.amountCollectedLessThenEqualTo}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              onChange={handleAmountFilterChange}
            />
          </div>
          <div>
            <SimpleDropDown
              label="label.fees.status"
              placeholder="placeholder.fees.status"
              name="status"
              value={pageObj?.filter?.status}
              onChange={handleFilterChange}
              options={FEE_RECEIPT_STATUS_OPTIONS}
              optionLabel="name"
              optionValue="code"
            />
          </div>

          <div className="col-span-2 flex gap-5 justify-end">
            <ButtonBox buttonType={BUTTON_TYPE.RESET_OUTLINE} onClick={onReset} />
            <ButtonBox buttonType={BUTTON_TYPE.SEARCH_OUTLINE} onClick={onSearch} />
          </div>
        </div>
      </div>
    );
  };

  const renderLoanApplicationNo = (rowData: IFeeCollectionReceiptRowData) => {
    const URL =
      linkBasePath +
      linkAuthRoute +
      LINK_LOAN_DETAIL +
      LINK_DDE_STAGE_AND_STATUS +
      "/" +
      rowData?.loan?.uid +
      LINK_DDE_STAGE_AND_STATUS_STAGES;
    return (
      <a className="flex items-center underline text-primary-sapphireBlue-900" href={URL}>
        {rowData?.loan?.lanId}
      </a>
    );
  };

  const renderBranch = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text className=" text-xs text-secondary-labelColor " label={rowData?.loan?.branch?.name} />
    );
  };

  const renderApplicantName = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text className=" text-xs text-secondary-labelColor " label={rowData?.loan?.applicantName} />
    );
  };

  const renderReceiptId = (rowData: IFeeCollectionReceiptRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.receiptRefNo} />;
  };

  const renderInstrumentType = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={FEE_MODE_LABEL?.[rowData?.smInstrumentType] ?? ""}
      />
    );
  };

  const renderInstrumentDate = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={formatDate(rowData?.instrumentDate)}
      />
    );
  };

  const renderUtrOrCheckNo = (rowData: IFeeCollectionReceiptRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.txnRefNo} />;
  };

  const renderAmount = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={numberFormatter(rowData?.amount, "en-IN", 0) ?? ""}
      />
    );
  };

  const renderRemark = (rowData: IFeeCollectionReceiptRowData) => {
    return <Text className=" text-xs text-secondary-labelColor " label={rowData?.remarks} />;
  };

  const renderStatus = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <Text
        className=" text-xs text-secondary-labelColor "
        label={getReceiptStatusLabel(rowData?.status)}
      />
    );
  };

  const renderReceiptActions = (rowData: IFeeCollectionReceiptRowData) => {
    return (
      <div className="grid grid-cols-2">
        <div className="">
          <IconButton
            icon={
              rowData.status === FEE_RECEIPT_STATUS.PENDING_DEPOSIT ? EditIcon : EditDisabledIcon
            }
            onClick={() => onEditReceipt(rowData?.uid)}
            tooltip="Edit"
            tooltipOptions={{
              position: "top",
              className: "text-xs font-Roboto bg-black text-white !p-1 rounded-md ",
              showDelay: "1000,",
              hideDelay: "300",
            }}
            disabled={rowData.status !== FEE_RECEIPT_STATUS.PENDING_DEPOSIT}
          />
        </div>
        <ButtonBox
          buttonType={BUTTON_TYPE.MENU}
          disabled={rowData.status !== FEE_RECEIPT_STATUS.PENDING_DEPOSIT}
          onClick={(e) => onToggleReceiptDeleteMenu(e, rowData)}
        />
      </div>
    );
  };
  /*
    -------------------------------------------------------
    Main Rendering
    -------------------------------------------------------
  */

  return (
    <div className="p-3 rounded-masterDataPageRadius bg-white flex flex-col gap-3 loan-listing-layout-body-content">
      <div className="flex w-full justify-between items-center">
        <Text
          label="label.fees.receipts"
          className="text-primary-ptext-1000 text-h2 font-bold flex"
        />
        <div className="flex gap-10">
          <ButtonBox buttonType={BUTTON_TYPE.SHARE_OUTLINE} onClick={onToggleSharePymtDialog} />
          <ButtonBox buttonType={BUTTON_TYPE.FILTER_OUTLINE} onClick={onToggleFilter} />
          <ButtonBox
            buttonType={BUTTON_TYPE.CREATE_NEW_RECEIPT}
            className="create-button"
            onClick={onCreate}
          />
        </div>
      </div>
      {pageObj?.showFilter && renderFilter()}
      <ReadOnlyTable
        standardDataFormatPromise={FeeService.getFeeCollectionReceipt}
        dataKey="uid"
        search={pageObj.tableSearch}
        ref={dataTableRef}
      >
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.applicationNo")}
          body={renderLoanApplicationNo}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6 "
          header={useTranslateText("label.fees.branch")}
          body={renderBranch}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs w-1/12  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.applicantName")}
          body={renderApplicantName}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
          header={useTranslateText("label.fees.receiptID")}
          body={renderReceiptId}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.instrumentType")}
          body={renderInstrumentType}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.instrumentDate")}
          body={renderInstrumentDate}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.utr/Chq.No.")}
          body={renderUtrOrCheckNo}
        />
        <Column
          className="text-primary-ptext-1000 text-right text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.amount")}
          body={renderAmount}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.remarks")}
          body={renderRemark}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  p-1 h-12 pl-6"
          header={useTranslateText("label.fees.status")}
          body={renderStatus}
        />
        <Column
          className="text-primary-ptext-1000 text-left text-xs  pr-4 h-12 pl-6"
          header={useTranslateText("label.fees.actions")}
          body={renderReceiptActions}
        />
        {/* <Column
          className="text-primary-ptext-1000 text-left text-xs p-1 h-12 pl-6"
          header={useTranslateText("label.loanListing.actions")}
          body={renderActions}
        /> */}
      </ReadOnlyTable>

      <SharePaymentDetailsDialog
        visible={pageObj?.sharePaymentDialog}
        onHide={onToggleSharePymtDialog}
        instrumentTypeOptions={pageObj.dropdownOptions.instrumentType}
        emailService={FeeService.feeCollectionSharePaymentDetailsByEmail}
      />
      <AddNewReceiptDialog
        visible={pageObj?.editReceiptDialog}
        isLoading={pageObj?.isReceiptDataLoading}
        onHide={onResetEditReceipt}
        instrumentTypeOptions={pageObj.dropdownOptions.instrumentType}
        module={MODULE_FOR_ADD_NEW_RECEIPT.FEES}
        data={{
          updateReceiptDetails: pageObj?.editReceipt,
        }}
        onSubmit={onSaveReceipt}
        ref={addReceiptRef}
      />
      <MenuBox
        ref={receiptDeleteMenuRef}
        model={receiptMenuModel}
        variant="SECONDARY"
        buttonClassName="!bg-transparent"
        className="width-full-menubox bg-white  w-max p-1.5 rounded-xl mt-0.5 border shadow-autoCompletePanel"
      />
      <WarningDialog
        visible={pageObj?.deleteReceipt?.showPopUp}
        className="w-76 !px-14"
        title={"heading.dde.fees.receiptDelete"}
        warningMessage={`description.dde.fees.receiptDelete`}
        onCancel={onResetWarningPop}
        onConfirm={() => {
          return onDeleteReceiptConfirm();
        }}
        onHide={() => {
          //
        }}
      />
      <ToastCustom
        position={"bottom-right"}
        ref={toastMessageRef}
        toastText={toastMessage}
        variant={toastMessageVariant}
      />
      <ErrorDialog
        onHide={() => {
          setShowDialogAPIError(false);
          setErrorMessage("");
        }}
        visible={showDialogAPIError}
        errorMessage={errorMessage}
      />
    </div>
  );
};

export default withRouter(PageReceipts);
